import React from 'react';
import './App.css';
import { Route, Switch } from 'react-router-dom';

import NavBar from './components/NavBar'
import NewNote from './components/NewNote'
import Notes from './components/Notes'
import ToggleButton from 'react-toggle-button'

class App extends React.Component {
  constructor(props) {
      super(props);
      this.state = {
        notes: [],
        submit: false,
        darkMode: false,
        updated: false
      };

      this.addNote = this.addNote.bind(this);
      this.removeNote = this.removeNote.bind(this);
      this.editNote = this.editNote.bind(this);
      this.toggleDark = this.toggleDark.bind(this);
  }

  componentDidMount() {
      this.setState({ notes: JSON.parse(localStorage.getItem('notes')) || [] });
  }

  addNote(note) {
    let notes = this.state.notes
    notes.push(note)
    this.setState({ notes })
    localStorage.setItem('notes', JSON.stringify(notes))
  }

  removeNote(index) {
    let notes = this.state.notes
    notes.splice(index, 1)
    this.setState({ notes })
    localStorage.setItem('notes', JSON.stringify(notes))
  }

  editNote(index, note) {
    let notes = this.state.notes
    notes[index].body = note
    this.setState({ notes, updated: true })
    localStorage.setItem('notes', JSON.stringify(notes))
  }

  toggleDark() {
    let darkMode = this.state.darkMode
    this.setState({darkMode: !darkMode})
  }

  render() {
    return (
      <div className="App" style={{backgroundColor: this.state.darkMode ? '#242424' : 'white', color: this.state.darkMode ? 'white' : '#212529'}}>
        <NavBar bg={this.state.darkMode ? 'dark' : 'primary'} darkMode={this.state.darkMode} toggleDark={this.toggleDark} mode={this.state.darkMode ? 'Light Mode' : 'Dark Mode'}/>
        <main style={{backgroundColor: this.state.darkMode ? '#242424' : 'white'}}>
            <Switch>
              <Route path="/" component={ () => <NewNote darkMode={this.state.darkMode} notes={this.state.notes} submit={this.state.submit} add={this.addNote} /> } exact />
              <Route path="/notes" component={ () => <Notes darkMode={this.state.darkMode} edit={this.editNote} remove={this.removeNote} notes={this.state.notes} /> } />
            </Switch>
        </main>
      </div>
    );
  }
}

export default App;
