import React from 'react';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';

import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';

let date;
let name;
let submit;

class NewNote extends React.Component {
	constructor(props) {
	    super(props);
      this.state = {
        isEmpty: false
      }
      let today = new Date()
      date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
	    this.addNote = props.add
      this.submit = props.submit
      this.notes = props.notes
      this.darkMode = props.darkMode
      this.handleChange = this.handleChange.bind(this);
	    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    submit = false
  }

  handleChange(event) {
    this.setState({[event.target.name]: event.target.value});
  }

  handleSubmit(event) {
    event.preventDefault();
    this.addNote({
    	body: this.state.body,
      currentDateTime: date
    })

    if(this.state.body.length >= 30) {
      name = this.state.body.substring(0, 30) + "..."
    } else {
      name = this.state.body
    }
    submit = !this.submit
  }

  render() {
    return (
      <div className="newNote-container">
      	<h1>Add note</h1>
      		<Form onSubmit={this.handleSubmit}>
      		  <Form.Group controlId="exampleForm.ControlTextarea1">
      		    <Form.Label>Body</Form.Label>
      		    <Form.Control required style={{backgroundColor: this.darkMode ? '#242424' : 'white', color: this.darkMode ? 'white' : '#212529'}} className="body" name="body" onChange={this.handleChange} as="textarea" rows="8" />
      		  </Form.Group>
      		  <Button style={{backgroundColor: this.darkMode ? 'rgb(23, 23, 23)' : '#007bff', border: this.darkMode ? '1px solid white' : '1px solid #007bff'}} className="submit-btn" variant="primary" type="submit">Submit</Button>
      		</Form>
          <Alert style={{display: submit ? 'block' : 'none'}} variant={this.darkMode ? 'dark' : 'success'}><b>{name}</b> has been added. <Link to="/notes">Click here to view.</Link></Alert>
      </div>
    );
  }
}

export default NewNote;
