import React from 'react';

import { Link } from 'react-router-dom';
import Card from 'react-bootstrap/Card';
import Accordion from 'react-bootstrap/Accordion';
import Button from 'react-bootstrap/Button';
import Badge from 'react-bootstrap/Badge';
import Jumbotron from 'react-bootstrap/Jumbotron';

class Notes extends React.Component {
	constructor(props) {
		super(props)
		this.notes = props.notes
		this.removeNote = props.remove
		this.addNote = props.add
		this.editNote = props.edit
        this.darkMode = props.darkMode
	}

	render() {
		return (
		  <div style={{backgroundColor: this.darkMode ? '#242424' : 'white', color: this.darkMode ? 'white' : '#212529'}}>
			  <Jumbotron style={{display: this.notes.length === 0 ? 'block' : 'none', backgroundColor: this.darkMode ? '#171717' : '#E9ECEF', color: this.darkMode ? 'white' : '#212529'}} className="jumbo">
				  <h1>Uh oh!</h1>
				  <p>
				    Seems like you have no notes! Click the button down below to the <b>new note</b> page
				    where you can add your first one.
				  </p>
				  <p>
				  <Link style={{backgroundColor: this.darkMode ? 'rgb(23, 23, 23)' : '#007bff', border: this.darkMode ? '1px solid white' : '1px solid #007bff'}} className="new-note-btn" to="/">New Note</Link>
				  </p>
			  </Jumbotron>
			  <div style={{display: this.notes.length === 0 ? 'none' : 'block'}} className="notes-container">
			  	<div className="heading" style={{backgroundColor: this.darkMode ? '#242424' : 'white', color: this.darkMode ? 'white' : '#212529'}}>
			  		<h1>Notes <span className="counter" ><Badge style={{backgroundColor: this.darkMode ? 'rgb(23, 23, 23)' : '#007bff', border: this.darkMode ? '1px solid white' : '1px solid #007bff'}} variant="primary">{this.notes.length}</Badge></span></h1>
	  			  	<Link style={{backgroundColor: this.darkMode ? 'rgb(23, 23, 23)' : '#007bff', border: this.darkMode ? '1px solid white' : '1px solid #007bff'}} className="new-note-btn2" to="/">New Note</Link>
	  			 </div>
			  	{this.notes.map((note, index) => {
			  		return (
			  			<div key={index} className="notes">
							<Accordion defaultActiveKey="1">
							  <Card>
							    <Card.Header style={{backgroundColor: this.darkMode ? '#242424' : 'white', color: this.darkMode ? 'white' : '#212529'}}>
							      <Accordion.Toggle as={Button} variant="link" eventKey="0" style={{backgroundColor: this.darkMode ? '#242424' : 'white', color: this.darkMode ? 'white' : '#212529'}}>
								    <div id='remove'><i onClick={() => this.removeNote(index)} className="fa fa-times"></i></div>
							        <div className="name">{note.body.length >= 20 ? note.body.substring(0, 30).replace(/(<([^>]+)>)/gi, " ").split('<')[0] + "..." : note.body.replace(/(<([^>]+)>)/gi, " ").split('<')[0]}<Badge className="pill" pill variant={this.darkMode ? "light" : "dark"}>{note.currentDateTime}</Badge></div>
							      </Accordion.Toggle>
							    </Card.Header>
							    <Accordion.Collapse eventKey="0">
							      <Card.Body style={{backgroundColor: this.darkMode ? '#242424' : 'white', color: this.darkMode ? 'white' : '#212529'}} contentEditable="true" onBlur={(e) => this.editNote(index, e.target.innerHTML)} dangerouslySetInnerHTML={{__html: note.body.replace(/\r?\n/g, '<br />')}}></Card.Body>
							    </Accordion.Collapse>
							  </Card>
							</Accordion>
						</div>
			  		)
			  	})}
			  </div>
		  </div>
		);
	}
}

export default Notes;
