import React from 'react';

import { Link } from 'react-router-dom';

import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';

class NavBar extends React.Component {
  constructor(props) {
    super(props)
    this.toggleDark = props.toggleDark
    this.darkMode = props.darkMode
  }

  render() {
    return (
        <Navbar collapseOnSelect expand="lg" bg={this.props.bg} variant="dark">
          <Navbar.Brand>Bootstrap Notes</Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="mr-auto"></Nav>
            <Nav>
              <div className="links">
                <Link to="/">New Note</Link>
                <Link to="/notes">Notes</Link>
                <a style={{cursor: 'pointer', color: 'white'}} onClick={this.toggleDark}>{this.props.mode}</a>
              </div>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
    );
  }
}

export default NavBar;
